// CUSTOM JS
$(document).ready(function(){

    if( $('#phone').length ){
	    $(function(){
	        $('#phone').phonecode({
	            preferCo: 'ru'
	        });
	    });
	}

	if( typeof($('#phone').attr('readonly')) !== false && typeof($('#phone').attr('readonly')) !== 'undefined'){
		$('#phone').parent().addClass('phone-readonly');
		// console.log("readonly");
	}

	// show and hide password
	$('.input-group__pass-status').on('click', function(){
		$(this).parent().find('.input-group__input').attr('type', function(index, attr){
			return attr == 'text' ? 'password' : 'text';
		});

		$(this).toggleClass('input-group__pass-status_show');
	});

	// RESEND SMS CODE FORM
	if ( $('#time').length ){
		// Timer
		function startTimer(duration, display) {
		    var timer = duration, minutes, seconds;

		    setInterval(function () {
		        minutes = parseInt(timer / 60, 10);
		        seconds = parseInt(timer % 60, 10);

		        minutes = minutes < 10 ? "0" + minutes : minutes;
		        seconds = seconds < 10 ? "0" + seconds : seconds;

		        display.text(minutes + ":" + seconds);

		        if (--timer < 0) {
		            timer = 0;
		        }

		    }, 1000);
		}

		jQuery(function ($) {
		    var oneMinute = 60, //60
		        display = $('#time');
		    startTimer(oneMinute, display);
		});

		window.setTimeout(function(){
		}, 61000);
	}



	/* VERIFICATION */

	var current_fs, next_fs, previous_fs;
	var left, opacity, scale;
	var animating;

	var fs_height = $('fieldset').outerHeight();

	$(".next").click(function(){		
		current_fs = $(this).parent().parent().parent().parent();
		next_fs = $(this).parent().parent().parent().parent().next();
		
		$(".timeline li").eq($("fieldset").index(next_fs)).addClass("active");
		$(".timeline li").eq($("fieldset").index(current_fs)).addClass("done").removeClass('active');
		
		next_fs.show().addClass('fadeInLeft'); 
		current_fs.hide();
	});

	$(".prev").click(function(){
		current_fs = $(this).parent().parent().parent().parent();
		previous_fs = $(this).parent().parent().parent().parent().prev();
		
		$(".timeline li").eq($("fieldset").index(current_fs)).removeClass("active").removeClass('done');
		$(".timeline li").eq($("fieldset").index(previous_fs)).addClass("active").removeClass('done');
		
		previous_fs.show().addClass('fadeInLeft'); 
		current_fs.hide();
	});

	$('.country-select').countrylist({
		preferCo: 'af'
	});

	// fullscreen mode in modal
	$('.open-fullscreen').on('click', function(e){
		e.preventDefault;

		$(this).parent().find('.fs__popup-thumbnail').toggleClass('img-fullscreen');
		
		$('.open-fullscreen-icon').toggle();
		$('.close-fullscreen-icon').toggle();
	});

	// View uploaded files
	$('.fs__row-link_view').on('click', function(){
		var openModal = $(this).attr('href');
		$(openModal).addClass('active');
	});

	// close modal
	$('.fs__popup-close').on('click', function(){
		$(this).parent().parent().parent().removeClass('active');
	});

	$('.fs__row-label .file-upload').on('change', function(event){
		$(this).parent().parent().find('.fs__row-link_view').attr('href', URL.createObjectURL(event.target.files[0]));
	});


	/* PAGE LITTLE SCRIPTS */

	// Menu links
	if( $(".nav .menu li.active").length ){
   		var anchor = $(".nav .menu li.active").offset().left;	
   		$('.nav .menu').scrollLeft( anchor + 20);
   	}

   	$('.lazy').lazy();


   	// Progress
   	var element = $('.progress__value');

   	$.each(element, function(index, value){
		var val = $(value).data("value");
		var max = $(value).data("max");
		var width = (val*100)/max;
		$(value).css('width', width+"%");

		$('.status .progress-title span').text(width + '%')
	});

   	// tables
	$('.table_desktop .table_portfolio__head').on('click', function(){

		$(this).parent().parent().find('.table__body').slideToggle();
		$(this).find('.table__expand').toggleClass('active');
	});

	$('.table_mobile .table_portfolio__head').on('click', function(){

		$(this).parent().parent().find('.table__body').slideToggle();
		$(this).find('img').toggleClass('active');
	});

   	$('.table__view-more').on('click', function (e) {
   		e.preventDefault;

   		$(this).parent().parent().find('.table-details').slideToggle();
   	});

   	// Accordion
  	$(".accordion .accordion__text").hide().prev().click(function() {
		$(this).parents(".accordion").find(".accordion__text").not(this).slideUp().prev().parent().removeClass("active");
		$(this).next().not(":visible").slideDown().prev().parent().addClass("active");
	});
	$(".accordion__item:first-child .accordion__text").slideDown();

	// Burger
    $('.burger').click(function() {
        $('.nav_screen').toggleClass('active');
    });

    $('.burger').click(function() {
        $('.burger').toggleClass('active');
    });

    $('.burger').click(function() {
        $('.hero').toggleClass('active');
    });

    // Tabs    
	if( $('.help').length && $(window).width() >= 768){

		$('.help__item-head').on('click', function(){
			$(this).parent().find('.accordion').slideToggle();
			$(this).find('.table__expand').toggleClass('active');
		});

		$('ul.tabs__caption').on('click', 'li:not(.active)', function() {
			$(this)
			  .addClass('active').siblings().removeClass('active')
			  .closest('div.tabs').find('div.tabs__content').removeClass('active').eq($(this).index()).addClass('active');
		});
	} 

	if ( $('.help').length && $(window).width() < 768 ){

		$('.help__item-head').parent().find('.accordion').slideUp();
		$('.help__item-head').find('.table__expand').removeClass('active');

		$('.help__item').first().find('.accordion').slideDown();
		$('.help__item').first().find('.table__expand').addClass('active');

		$('.help__item-head').on('click', function(){
			$('.help__item-head').parent().find('.accordion').slideUp();
			$('.help__item-head').find('.table__expand').removeClass('active');

			if( !$(this).next().is(":visible") ){
				$(this).next().slideDown();
				$(this).find('.table__expand').addClass('active');
			}
		});
	}

	// Upload avatar
	$('#upload_avatar').on('change', function () {
		$('.upload-avatar-form').submit();
	});

	// Update data profile form
	if( $('.update-data-form').length ){
		var email = $('#email').val();
		var address = $('#address').val();

		$('#updateDataCancel').on('click', function(){
			$('#email').val(email);
			$('#address').val(address);
		});
	}


	if( $('#settingsDataCancel').length ){ 

		$('#settingsDataCancel').on('click', function(){
			$('#password').val("");
			$('#new-password').val("");
			$('#new-password-repeat').val("");
		});
	}

	// Change email
	$('#change-email-button').on('click', function(){
		var newEmail = $('#new-email').val();

		if (newEmail.length == 0){
			$('#new-email').parent().find('.input-group__help').css('display', 'block').html('E-mail field can\'t be empty');
			return false;
		}
		else if (!/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(newEmail)){
			$('#new-email').parent().find('.input-group__help').css('display', 'block').html('E-mail field Incorrect');
        	return false;
        } 

    	$('.input-group__help').css('display', 'none');

    	$.ajax({
			url: '../ajax/test.php',
			method: 'POST',
	        dataType: 'html',
	        cache: false,
			data: {
				newEmail: newEmail
			},
			success: function(){
				$('[data-remodal-id=change-email-done-modal]').remodal().open();
			},
			error: function(){
				// $('[data-remodal-id=change-email-done-modal]').remodal().open();
			}
		});
	});


	// 2Way auth setup
	if($('#switcher_2way-auth').length){
		$(this).on('change', function(){
			if($('#switcher_2way-auth').is(":checked")){
				$('.switcher__status').html("on").addClass('switcher__status_on');
			} else {
				$('.switcher__status').html("off").removeClass('switcher__status_on');
			}
		});
	}
});


if ( $('.nav .menu').length ){
	window.onscroll = function() {myFunction()};

	var anchor = $(".nav .menu li.active").offset().left;
	var navbar = document.querySelector(".nav");
	var sticky = navbar.offsetTop;

	function myFunction() {
	  	if (window.pageYOffset >= 78) {
	    	navbar.classList.add("sticky");
	    	$('.hero').css('margin-top', 78+"px");
	 	} else {
	    	navbar.classList.remove("sticky");
	    	$('.hero').css('margin-top', 0+"px");
	    
			$('.nav .menu').scrollLeft( anchor + 80);
	  }
	}
}